import Vector from "./utils/vector";
import { drawDot, randomBetween } from './utils/utils';
import { getPointOnArc } from './utils/paths';

const QUARTER_PI = Math.PI * 0.5;

class Point {
  constructor(x, y, angle, maxPinch) {
    this.position = new Vector({ x, y });
    this.angle = angle;

    this.left = this.getParallel(QUARTER_PI * 3, maxPinch);
    this.right = this.getParallel(QUARTER_PI, maxPinch);
  }

  getParallel(offset, maxPinch) {
    const { x, y } = getPointOnArc(this.position.x, this.position.y, this.angle + offset, randomBetween(5, maxPinch));
    return new Vector({ x, y });
  }

  moveTo() {
    ctx.moveTo(this.position.x, this.position.y);
  }

  curve(nextPoint) {
    ctx.bezierCurveTo(
      this.right.x, this.right.y,
      nextPoint.left.x, nextPoint.left.y,
      nextPoint.position.x, nextPoint.position.y,
    );
  }

  debugDraw() {
    drawDot(this.position.x, this.position.y);
    drawDot(this.left.x, this.left.y, 'green');
    drawDot(this.right.x, this.right.y, 'blue');
  }
}

export default Point;
