import { getPointOnArc } from './utils/paths';
import { randomBetween } from './utils/utils';

import Point from './Point';

class Circle {
  constructor(cx, cy, maxRadius, segments = 20, { color = 'red', fill = false } = {}) {
    this.path = this.getPath(cx, cy, maxRadius, segments);

    this.fill = fill;
    this.color = color;
  }

  getPath(cx, cy, maxRadius, segments = 20) {
    let _path = [];

    let segmentAngle = (Math.PI * 2) / segments;

    for (let i = 0; i < segments; i++) {
      const { x, y } = getPointOnArc(cx, cy, segmentAngle * i, randomBetween(maxRadius * 0.5, maxRadius));

      _path.push(new Point(x, y, segmentAngle * i, (maxRadius / segments) * 2));
    }

    return _path;
  }

  draw() {
    ctx.save();
    if (this.fill) {
      ctx.globalAlpha = 0.85;
    }

    ctx.beginPath();
    this.path[0].moveTo();
    this.path.forEach((p, index) => {
      if (index < this.path.length - 1) {
        p.curve(this.path[index + 1]);
      } else {
        p.curve(this.path[0]);
      }
    })

    ctx.fillStyle = this.color;
    ctx.strokeStyle = this.color;

    if (this.fill) {
      ctx.fill();
    } else {
      ctx.stroke();
    }
    ctx.restore();
  }
}

export default Circle;
