import * as tome from 'chromotome';

import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import { randomBetween } from './utils/utils';
import Circle from './Circle';

const WIDTH = 480;
const HEIGHT = 480;

let palette = tome.getRandom();

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

const createPaths = () => {
  const circles = [];

  for (let i = 0; i < 5; i++) {
    let size = randomBetween(0, WIDTH / 2);

    circles.push(new Circle(
      randomBetween(size, WIDTH - size),
      randomBetween(size, HEIGHT - size),
      size,
      randomBetween(4, 10), {
        fill: Math.random() > 0.5,
        color: palette.colors[Math.floor(randomBetween(0, palette.colors.length))]
      }
    ))
  }

  return circles;
}

let paths = createPaths();

// const path = getIrregularCircle(WIDTH / 2, HEIGHT / 2, WIDTH / 4, 5);


app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);
  paths.forEach(circle => {
    circle.draw();

    // path.forEach((p, index) => {
    //   p.debugDraw();
    // })
  });
});

app.canvasEl.addEventListener('click', e => {
  palette = tome.getRandom();
  paths = createPaths();
});

app.start();
